.Department {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  width: 60%;
  height: 40px;
  line-height: 40px;
  color: #ffffff;
  background: transparent;
  text-align: center;
}
.Department .Department-item {
  -ms-flex: 1 1;
      flex: 1 1;
  display: inline-block;
}
.Department .Department-item .icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 5px;
  border-radius: 50%;
}
.Department .Department-item .icon.red {
  background: #ff0000;
}
.Department .Department-item .icon.yellow {
  background: #ffff00;
}
.Department .Department-item .icon.blue {
  background: #5c8af9;
}
.Department .Department-item .name {
  display: inline-block;
}



