.Shifter {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  top: 40px;
  right: 20%;
  width: 30%;
  height: 60px;
  line-height: 60px;
  color: #7a9ac4;
  text-align: center;
}
.Shifter li {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
}
.Shifter li span {
  position: absolute;
  top: 0;
  right: 0;
  color: #ffffff;
}
.Shifter li:last-child span {
  display: none;
}
.Shifter li.active {
  color: #ffffff;
}



