.MainBody {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-left: 10px;
  padding-right: 10px;
  background: -webkit-gradient(linear, left top, left bottom, from(#0e468c), to(#396b96));
  background: -webkit-linear-gradient(top, #0e468c, #396b96);
  background: -o-linear-gradient(top, #0e468c, #396b96);
  background: linear-gradient(to bottom, #0e468c, #396b96);
}



