.Chart {
  position: fixed;
  top: 150px;
  left: 80px;
  right: 80px;
  bottom: 80px;
}
.Chart .ChartTable {
  height: 100%;
  border: 1px solid #547ead;
}
.Chart .row {
  position: absolute;
  width: 100%;
  border-bottom: 1px solid #547ead;
}
.Chart .TableNumbers {
  position: absolute;
  top: 0;
  left: -30px;
  height: 100%;
  color: #ffffff;
}
.Chart .TableNumbers .zero {
  position: absolute;
  bottom: -10px;
}
.Chart #canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.Chart .dataList {
  display: -ms-flexbox;
  display: flex;
  padding-top: 15px;
  color: #ffffff;
}
.Chart .dataList .listItem {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}
.Chart .dataList .listItem:first-child {
  margin-left: -30px;
  text-align: left;
}
.Chart .dataList .listItem:last-child {
  text-align: right;
}



